/* estates-main */
#estates-main{
	border: solid var(--third-color) 5px;
  	height: 100vh;
  	width: 100%;
  	display: flex;
  	flex-direction: column;
  	justify-content: space-around;
 	align-items: center;
 	overflow-y: scroll;
 	box-sizing: border-box;
 	overflow-x: hidden;
  padding: 1%;
}
#estates-main::-webkit-scrollbar {
  width: 8px;
}
/* scroll bar thumb (the actual scroll) */
#estates-main::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
#estates-main::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

/* estates-main h1 */
#estates-main-div1{

  width: 100%;
  height: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1%;
}

#estates-main-div1-h1{
  padding: 1.5%;
  background-color: var(--fourth-color);
  color: var(--third-color);
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: underline;
  text-decoration-color: var(--third-color);
}

/* index estates main content article */
#estates-main-article1{

    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

#estates-main-article1-p1-loading{
  margin-left: 48%;
}

#estates-main-article1-h2{
    background-color: var(--third-color);
    color: var(--fourth-color);
    padding: 1%;
    font-size: 200%;
    min-width: 30%;
}

#estates-main-article1-p1{
  font-size: 130%;
  line-height: 30px;
}

/* index estates main section1 */
#estates-main-section1{

  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

#estates-main-section1-loading{
  margin-left: 48%;
}

#estates-main-section1-h2{
    background-color: var(--third-color);
    color: var(--fourth-color);
    padding: 1%;
    font-size: 200%;
    min-width: 30%;
}

#estates-main-section1-p1{
  width: 100%;
  height: auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
 font-size: 130%;
  line-height: 30px;
}

/* index estates main section1 articles */
.estates-main-section1-article1{
  border-bottom: solid var(--third-color) 2px;
  border-right: solid var(--third-color) 2px;
  width: 100%;
  height: 60vh;
  margin-bottom: 2%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

}



/* index estates main section1 articles div1*/
.estates-main-section1-article1-div1{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border-right: solid var(--third-color) 2px;
}


.estates-main-section1-article1-div-h3{
  height: 15%;
  background-color: var(--fourth-color);
  color: var(--third-color);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 1%;
}

.estates-main-section1-article1-div-p{
  font-size: 115%;
  width: 95%;
  height: 40%;
  overflow-y: scroll;
  padding: 2%;
  box-sizing: border-box;
}
.estates-main-section1-article1-div-p::-webkit-scrollbar {
  width: 8px;
}
/* scroll bar thumb (the actual scroll) */
.estates-main-section1-article1-div-p::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
.estates-main-section1-article1-div-p::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */

}

.estates-main-section1-article1-div-div1{
  height: 25%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.estates-main-section1-article1-div2-div2-a{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5%;
  background-color: var(--third-color);
  color: var(--fourth-color);
  text-decoration: none;
  transition: 0.5s;
  margin-right: 2%;

}
.estates-main-section1-article1-div2-div2-a:hover{
    background-color: var(--fourth-color);
    color: var(--third-color);
}

.estates-main-section1-article1-div2-div2-div1{

  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 95%;
  background-color: var(--fourth-color);
  color: var(--third-color);
}

.estates-main-section1-article1-div2-div2-span1-p{
  height: 100%;
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.estates-main-section1-article1-div2-div2-span1-p2{
  width: 80%;
  overflow: scroll;
}

/* index estates main section1 articles images*/
.estates-main-section1-article1-div2{
  background-color: rgba(7, 12, 43,0.9);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.estates-main-section1-article1-div2-div1{
  height: 95%;
  width: 95%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
   background-color: rgba(0, 0, 0, 0.5);
}

.estates-main-section1-article1-div2-div1-img {
  height: 85%;
  width: 85%;
  object-fit: contain;
  object-position: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 1s ease-in-out; /* smooth fade */
}
.estates-main-section1-article1-div2-div1-img.show {
  opacity: 1;
}


.estates-main-section1-article1-div2-div1-img-backward{
  position: absolute;
  left: 0;
  top: 50%;
font-size: 200%;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}
.estates-main-section1-article1-div2-div1-img-backward:hover{
  color: white;
}
.estates-main-section1-article1-div2-div1-img-forward{
  position: absolute;
  right: 0;
  top: 50%;
  font-size: 200%;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}
.estates-main-section1-article1-div2-div1-img-forward:hover{
  color: white;
}

.estates-main-section1-article1-div2-div1-img-footer{
  position: absolute;
  bottom: 0;
  height: 5%;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: scroll;
}

.estates-main-section1-article1-div2-div1-img-footer-counter{
  margin-left: 1%;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: var(--fourth-color);
  cursor: pointer;
}

.estates-main-section1-article1-div2-div1-img-footer-counter-selected{
  background-color: white;
}














